function resetViewport () {
  try {
    // ie > 8
    if (document.head) {
      const screenWidth = window.screen && window.screen.width
      const htmlWidth = 1000

      let meta = document.querySelector('meta[name=viewport]')
      if (!meta) {
        meta = document.createElement('meta')

        meta.setAttribute('name', 'viewport')
      }

      const content = screenWidth < htmlWidth ? `width=device-width, initial-scale=${screenWidth / htmlWidth}, user-scalable=no, viewport-fit=cover` : 'width=device-width,initial-scale=1.0'

      meta.setAttribute('content', content)

      if (!meta) {
        document.head.insertBefore(meta, document.querySelector('title'))
      }
    }
  } catch (e) {
    console.log(e)
  }
}

resetViewport()

window.onresize = resetViewport

